*{
  box-sizing: border-box;
}

body{
  width: 100%;
  padding-left: 90px;
  padding-right: 90px;
  font-family: Roboto, Arial, sans-serif;
  background: #FBF5F4;
}

ul,li{
  list-style: none;
  margin: 0;
  padding: 0;
}

a{
  text-decoration: none;
}

button{
  background: none;
  border: none;
  cursor: pointer;
}